import axios from './axios';

export function getUnityList(params) {
  return axios.get('/unity/list', { params });
}
export function uploadResource(params) {
  const formdata = new FormData();
  Object.keys(params).forEach((item) => {
    formdata.append(item, params[item]);
  });
  return axios.post('/unity/upload', formdata, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
}
export function uploadConfigFile(params) {
  const formdata = new FormData();
  Object.keys(params).forEach((item) => {
    formdata.append(item, params[item]);
  });
  return axios.post('/unity/config', formdata, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
}
export function deleteUnity(id) {
  return axios.delete('/unity', { params: { id } });
}
